.event-details{
  background-color: #555B6E;
  margin-top: -40px;
  box-shadow: 0px 5px 10px 8px rgba(162, 162, 162, 0.262);
  border: 5px solid#FF934F;
  border-top: none;
  height: 260px;
}
.event-details *{
  color: #FF934F;
  padding-bottom: 5px;
}
.event-details h2{
  margin-top: 0;
}
.attendButton{
  background-color: #FF934F;
  color: #555B6E;
}
.attendingButton{
  background-color: #87BCDE;
  color: #555B6E;
}
.acquaintance-img{
  width: 50px;
  border-radius: 50%;
  border: 1px solid #FF934F;
}
.messageButton{
  align-self: center;
  margin-left: 10px;
  margin-bottom: 10px;
  background-color: #FF934F;
  color: #555B6E;
  align-self: flex-end;

}
.messageButton:hover{
  align-self: center;
  margin-left: 1%;
  margin-bottom: 10px;
  background-color: #555B6E;
  color: #FF934F;
  align-self: flex-end;
}
.message-input-and-button{
  display: flex;
  align-content: center;
}
.message-box{
  margin-top: 20px;
}
.other-user-message{
  background-repeat: no-repeat;
  background-size: contain;
  min-width: 15%;
  max-width: 180px;
  min-height: 150px;
  justify-content: flex-start;
  align-content: flex-start;
  display: flex;
  background-image: url('/images/other-user-message.png');

}
.user-message{
  background-repeat: no-repeat;
  background-size: contain;
  min-width: 15%;
  max-width: 180px;
  min-height: 150px;
  justify-content: flex-end;
  align-content: flex-end;
  display: flex;
  background-image: url('/images/user-message-box.png');

}
.user-chat-container{
  display: flex;
  justify-content: flex-end;
}
.content-message-container{
  display: flex;
  justify-content: center;
  align-content: center;
  width: 90%;
  height: 80%;
  overflow-y: scroll;
  margin-right: 10px;
}
.chat-container{
  display: flex;
  justify-content: flex-start;
}
input[type=text]:focus {
  border-bottom: 1px solid #87BCDE !important;
  box-shadow: 0 1px 0 0 #87BCDE !important
}
.other-user-message-content{
color: #555B6E;
size: 30px;
margin-left: 5px;
width: 80%;
word-wrap: break-word;
}
.user-message-content{
  color: #555B6E;
  size: 30px;
  margin-right: 10px;
  width: 80%;
  align-self: flex-start;
  word-wrap: break-word;
  }

  #chat-box{
    box-shadow: 0px 5px 10px 8px rgba(162, 162, 162, 0.262);
    border: 1px solid #555B6E;
    height: 400px;
    overflow-y: scroll;
  }